.App {
  background-color: #222;
  text-align: center;
  height: 100vh;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App-header {
  padding: 10px;
  color: white;
}

.App-title {
  display: inline;
  font-size: 1.5em;
  font-weight: 100;
  margin: 0 5px;
  padding: 0;
}

#btn-open-image {
  position: fixed;
  bottom: 1em;
  right: 1em;
  width: 3em;
  height: 3em;

  font-size: 1em;
  color: white;
  background: #f50057;
  border: none;
  border-radius: 50%;
  -webkit-box-shadow: black 0 5px 10px;
          box-shadow: black 0 5px 10px;
}

#btn-open-image:hover {
  cursor: pointer;
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

#open-button {
  font-size: 1.5em;
  color: white;
  margin: 0 5px;
}

#photo-block {
  border-radius: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0 auto;
  margin-bottom: 10px;
  max-width: 80%;
  height: 55vh;
  /* centerize the photo */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  /* XXX: workaround for backdrop-filter */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

#photo-placeholder {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  background: #999;
}

#photo-placeholder > p {
  font-size: 3em;
  font-weight: 100;
  font-style: italic;
  color: #bbb;
  margin: 0 auto;
}

#photo {
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
}

#histogram-chart {
  border: 1px solid #999;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  height: 30vh;
}

.histogram-channel {
  width: 80%;
  margin: 0 auto;
}

.histogram-channel > ul {
  list-style: none;
  padding: 0;
}

.histogram-channel ul li {
  display: inline-block;
  margin-right: 10px;
}

.histogram-channel ul li:last-child {
  margin-right: 0;
}



